html{
  /* background: rgb(221,238,247); */
  background: linear-gradient(166deg, rgba(221,238,247,0.1) 0%, rgba(172,208,227,0.1) 96%, rgba(172,208,227,0.1) 100%);

  min-height: 100vh;
}
*{
  font-family: 'Montserrat', sans-serif;
}

.MuiButton-root{
  border:1px solid black !important;
  border-radius:3px;
}
h1 > a,h1 > a:hover{
  color:black;
  text-decoration: none;
}
.nft-page-container{
  height:100vh;
  width:100%;
}
.meta-header{
  max-width: 100% !important;
  -webkit-box-shadow: 0px 0px 9px 3px rgba(41,41,41,.25);
-moz-box-shadow: 0px 0px 9px 3px rgba(41,41,41,.25);
box-shadow: 0px 0px 9px 3px rgba(41,41,41,.25);

}

.meta-link-header{
  font-family: 'Cinzel Decorative', 'cursive' !important;
}

.meta-header-wallet{
  font-size:20px;
  line-height: 1.5;
}
.meta-header-wallet svg{
  transform:translatey(5px);
}

@media (max-width:950px){
  .card-nft.card{
    height:560px !important;
    margin-top:-50px!important;
    margin-bottom:50px!important;
  }
  .nft-page-container{
    height:50vh;
    width:100%;
  }
  .card-choices{
    display:inline !important;
    height:auto !important;
  }
  .card-choices>ul{
    margin-top:50px;
    margin-bottom:100px !important;
        padding-left:0;

  }
  li{
    margin-top:11px!important;
    margin-bottom:11px!important;
  }
  .MuiButton-text{
    margin-bottom:15px !important;
  }
  h1{
    font-size:20px;
  }
}
@media (min-width: 750px){
  .MuiContainer-maxWidthSm {
      max-width: 750px !important;
  }
  }

  .card-nft-enter {
    opacity: 0;
    transition: opacity 400ms ease-in;
  }
  
  .card-nft-enter.card-nft-enter-active {
    opacity: 1;
  }
  
  ul {
    list-style-type: none;
    /* padding-left:0; */
  }
   li{
    margin:3.5px;
    cursor:pointer;
   }
 
   li:first-child{
    margin:2px;
   }
   
   li > span{
    transition:all 50ms ease-in-out !important;
   }
    li > span:hover{
    text-shadow:0.5px 0 rgba(0, 0, 0, 0.5),0.5px 0 rgba(0, 0, 0, 0.5);
    }

   .item-selected{
    text-shadow:1px 0 rgb(0, 0, 0),1px 0 rgb(0, 0, 0) !important;
  }

@keyframes gradient {
    0% {
        background-position: 0% 50%!important;
    }
    50% {
        background-position: 100% 50%!important;
    }
    100% {
        background-position: 0% 50%!important;
    }
}
#root > div{
  min-height: 100vh;
  
}


.max-height{
  /* background-position: 0% 10%!important; */
  /* animation: gradient 40s ease infinite !important; */
  transition:all 400ms ease-in-out !important;

}

.crop-text{
  overflow: hidden;
white-space: nowrap; /* Don't forget this one */
text-overflow: ellipsis;
}

